import React from 'react';
import AppLayout from '/components/AppLayout/AppLayout'

export default class extends React.Component {
    render(){
        return(
            <AppLayout>
                <h1>Plan De Investigación</h1>
                <p>
                Nuestra propuesta se basa en probar nuevas formas de entregar el contenido a los niños. Para ello, se hablará con los maestros para que cambien la manera en la que exponen la información, buscando hacer la experiencia más divertida y los conceptos más fáciles de aprender. Tenemos tres métodos educativos poco convencionales que estaremos poniendo a prueba, descritos abajo:
                </p>
                <p>
                - Gamificación: Es una estrategia que consiste en integrar dinámicas y elementos normalmente utilizados en juegos para mejorar el aprendizaje. Es una parte natural de los niños el jugar, e incorporarlo al aprendizaje es una buena manera de hacer que les guste más. Al utilizar este método los errores pierden peso, pues los estudiantes pensarán que "es solo un juego". Sin embargo, introducir elementos de juegos no es lo mismo que aprender con juegos, dos conceptos muy diferentes que pueden ser confundidos con facilidad. Entre los elementos de juego que pueden ser utilizados, destacan estos: Incorporar metas y objetivos, ya que aumentan la motivación; Libertad de elegir y equivocarse, lo que propicia confianza y participación al perderse el miedo;  cooperación y/o competencia, dependiendo de la materia/actividad; recompensas y factor sorpresa, otros motivadores; tiempo limitado, lo cual los presiona a dar su máximo; progreso, que inspira a los alumnos a desarrollar actividades cada vez más complejas; estatus, que les permite saber cómo van en proporción a los demás; retroalimentación, para guiar al alumno y mostrar su comportamiento y acciones; entre otros.
                </p>
                <p>
                - Autodidactismo: Se refiere a la habilidad del estudiante de aprender por sí mismo. Con esta estrategia se busca darle a los estudiantes un rol activo en el aprendizaje, en lugar de estar sentados escuchando al profesor "vomitar" la información. Esto hace que los alumnos se interesen más en el proceso de aprendizaje, pues lo verán como algo más divertido que su antigua visión de la escuela, estar sentados sin hacer nada. Sin embargo, un autodidactismo completo no es viable, por lo que optaremos por utilizar una estrategia conocida como "Aula invertida". Este método se trata de darles el material de clase como tarea, y que ellos lo investiguen en casa. En este caso, el tiempo en el aula el profesor lo utilizaría para responder dudas.
                </p>  
                <p>
                - Aprendizaje basado en retos: Trata de hacerles ver a los alumnos que el material aprendido en clase es relevante, y de ponerlos a resolver problemáticas de la vida real con los conocimientos adquiridos. Este método está relacionado con las preguntas que suelen hacer los alumnos a sus profesores, como "¿Y para qué me va a servir esto en la vida?", ya que enlaza el material visto en clase con situaciones de la vida real. Este método genera mucha motivación, pues los alumnos están haciendo su trabajo con un propósito en mente. Este método se resuelve por partes, empezando con el reto, luego la generación de ideas para solucionarlo, y por último investigar, revisar y probar la posible solución hasta llegar a la óptima. 
                </p>
                <p>
                A través de estas propuestas, esperamos obtener mejores resultados implementando las 3 que los que hay ahora en día en las escuelas.
                </p>
            </AppLayout>
        )
    }
}


